import { useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import multicall from 'utils/multicall'
import { getMasterChefAddress } from 'utils/addressHelpers'
import masterChefABI from 'config/abi/masterchef.json'
import { farmsConfig } from 'config/constants'
import { FarmConfig } from 'config/constants/types'
import useRefresh from './useRefresh'
import { useChainId } from './useTheme'

export interface FarmWithBalance extends FarmConfig {
  balance: BigNumber
}

const useFarmsWithBalance = () => {
  const [farmsWithBalances, setFarmsWithBalances] = useState<FarmWithBalance[]>([])
  const { account } = useWallet()
  const { fastRefresh } = useRefresh()
  const { chainId } = useChainId()

  useEffect(() => {
    const fetchBalances = async () => {
      const calls = farmsConfig
        .filter((x) => !x.ignore && x.type === 0)
        .map((farm) => ({
          address: getMasterChefAddress(farm.type),
          name: 'pendingReward',
          params: [farm.contractPid, account],
        }))

      const rawResults = await multicall(chainId, masterChefABI, calls)
      const results = farmsConfig.map((farm, index) => ({ ...farm, balance: new BigNumber(rawResults[index]) }))

      setFarmsWithBalances(results)
    }

    if (account) {
      fetchBalances()
    }
  }, [account, fastRefresh, chainId])

  return farmsWithBalances
}

export default useFarmsWithBalance
