import React, { useEffect, useState } from 'react'
import BigNumber from 'bignumber.js/bignumber'
import styled from 'styled-components'
import { LinkExternal, BaseLayout, Card, CardBody, Heading } from 'uikit'
import FlexLayout from 'components/layout/Flex'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import useIsMember from 'hooks/useIsMember'
import Page from 'components/layout/Page'
import Value from 'components/Value'
import { usePriceABnb, usePriceDaoBusd, usePriceYieldBusd } from 'state/hooks'
import Label from 'components/Label'
import { useSupplyState } from 'hooks/useAgentInfo'
import { getTokenAddress, getYieldTokenAddress, getCTokenAddress } from 'utils/addressHelpers'
import FarmStakingCard from './components/FarmStakingCard'
import CakeStats from './components/CakeStats'
import JoinCard from './components/JoinCard'

const Hero = styled.div`
  align-items: center;
  background-image: url('/images/logo.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: auto;
  margin-bottom: 32px;
  padding-top: 116px;
  text-align: center;
  min-height: 200px;
`

const Cards = styled(BaseLayout)`
  align-items: stretch;
  justify-content: stretch;
  margin-bottom: 48px;

  & > div {
    grid-column: span 6;
    width: 100%;
  }

  ${({ theme }) => theme.mediaQueries.sm} {
    & > div {
      grid-column: span 8;
    }
  }

  ${({ theme }) => theme.mediaQueries.lg} {
    & > div {
      grid-column: span 6;
    }
  }
`

const CenterHeading = styled(Heading)`
  text-align: center;
`

const StyledCardHeader = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
  padding: 10px;
`

const deadBalance = (aAddress, address = '0x000000000000000000000000000000000000dead') =>
  `https://api.bscscan.com/api?module=account&action=tokenbalance&contractaddress=${aAddress}&address=${address}&tag=latest&apikey=VSHI3UDSF7XUCTQ5ZKY6SBQ985HACXNJUN`

const bigA = new BigNumber(10).pow(18)

const useFetch = (address: string) => {
  const [data, setData] = useState(new BigNumber(0))

  // empty array as second argument equivalent to componentDidMount
  useEffect(() => {
    async function fetchData() {
      const uri = deadBalance(address)
      const response = await fetch(uri)

      const { result } = await response.json()
      setData(new BigNumber(result).div(bigA))
    }
    fetchData()
  }, [address])

  return data
}

const TokenCard: React.FC<any> = ({ name, total, burned, price, address }) => {
  return (
    <Card>
      <CardBody>
        <CenterHeading size="xl">{name}</CenterHeading>
        <StyledCardHeader>
          <Value value={total} decimals={3} />
          <Label text="Total Supply" />
        </StyledCardHeader>
        <StyledCardHeader>
          <Value value={burned} decimals={3} />
          <Label text="Burned" />
        </StyledCardHeader>
        <StyledCardHeader>
          <Value value={price} decimals={3} prefix="$" />
          <Label text="Price" />
        </StyledCardHeader>
        <StyledCardHeader>
          <LinkExternal small href={`https://dex.slime.finance/#/swap?outputCurrency=${address}`} mr="16px">
            Buy {name}
          </LinkExternal>
        </StyledCardHeader>
      </CardBody>
    </Card>
  )
}

const BscHome: React.FC = () => {
  const [isAgent] = useIsMember()
  const { account } = useWallet()
  const [totalA, totalB, totalC] = useSupplyState()
  const a = getTokenAddress()
  const b = getYieldTokenAddress()
  const c = getCTokenAddress()
  const tokenPriceUsd = usePriceABnb()
  const yieldPrice = usePriceYieldBusd()
  const daoPrice = usePriceDaoBusd()
  const burnedBalance = useFetch(getTokenAddress())
  const burnedCBalance = useFetch(getCTokenAddress())
  const burnedYBalance = useFetch(getYieldTokenAddress())

  return (
    <>
      <FlexLayout>
        <TokenCard
          name="A009"
          total={totalA.toNumber()}
          burned={burnedBalance.toNumber()}
          price={tokenPriceUsd.toNumber()}
          address={a}
        />
        <TokenCard
          name="A010"
          total={totalB.toNumber()}
          burned={burnedYBalance.toNumber()}
          price={yieldPrice.toNumber()}
          address={b}
        />
        <TokenCard
          name="C100"
          total={totalC.toNumber()}
          burned={burnedCBalance.toNumber()}
          price={daoPrice.toNumber()}
          address={c}
        />
      </FlexLayout>
      <Cards>
        {account && isAgent ? <FarmStakingCard /> : <JoinCard />}
        <CakeStats />
      </Cards>
    </>
  )
}

const FantomHome: React.FC = () => {
  return (
    <>
      <CenterHeading size="xl">Ghost Agency</CenterHeading>
      <CenterHeading size="lg">Soon sir...</CenterHeading>
    </>
  )
}

const Home: React.FC = () => {
  const { chainId } = useWallet()

  return (
    <Page>
      <Hero />
      {chainId === 250 ? <FantomHome /> : <BscHome />}
    </Page>
  )
}

export default Home
