import { useEffect, useState } from 'react'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import multicall from 'utils/multicall'
import { getMasterChefAddress } from 'utils/addressHelpers'
import masterChefABI from 'config/abi/masterchef.json'
import { farmsConfig } from 'config/constants'
import useRefresh from './useRefresh'
import { useChainId } from './useTheme'

const useAllEarnings = () => {
  const [balances, setBalance] = useState([])
  const { account }: { account: string } = useWallet()
  const { fastRefresh } = useRefresh()
  const { chainId } = useChainId()

  useEffect(() => {
    const fetchAllBalances = async () => {
      const calls = farmsConfig
        .filter((x) => !x.ignore)
        .map((farm) => ({
          address: getMasterChefAddress(farm.type),
          name: 'pendingReward',
          params: [farm.contractPid, account],
        }))

      const res = await multicall(chainId, masterChefABI, calls)

      setBalance(res)
    }

    if (account) {
      fetchAllBalances()
    }
  }, [account, fastRefresh, chainId])

  return balances
}

export default useAllEarnings
